import { DividerActionConfig } from '../../types/actions/base';

function isDivider<T>(
  item: T | DividerActionConfig,
): item is DividerActionConfig {
  return (item as DividerActionConfig).type === 'divider';
}

export function groupActions<T>(actions: (T | DividerActionConfig)[]): T[][] {
  if (!actions || actions.length === 0) {
    return [];
  }

  const groups: T[][] = [];
  let currentGroup: T[] = [];

  for (const item of actions) {
    if (isDivider(item)) {
      if (currentGroup.length > 0) {
        groups.push(currentGroup);
        currentGroup = [];
      }
    } else {
      currentGroup.push(item as T);
    }
  }

  if (currentGroup.length > 0) {
    groups.push(currentGroup);
  }

  return groups;
}
